import React, { Component } from 'react';
import styled from 'styled-components';
import { media } from '../styles/style-utils';
import { format } from 'util';
import { EmailIcon, CheckIcon } from './Icons';
import axios from 'axios';

const Container = styled.div`
    text-align:center;
    width: 100%;
    padding: 10px;
    ${media.tablet`
        width: 830px;
        padding: 55px;
        margin: 0 auto;
    `}
`

const ShadowDiv = styled.div`
    display: inline-block;
    padding: 20px 17px;
    background: #fff;
    box-shadow: 0 2px 4px rgba(0,0,0,.11);
    border-radius: 8px;
    width: 100%;
    form{
        display:grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 0;
        ${media.tablet`
            grid-template-columns: 2fr 1fr;
        `}
        input{
            width: 100%;
            height: 46px;
            font-size: 17px;
            font-weight: 500;
            color: #676767;
            border: 3px solid #E7E9EC;
            border-right: 0;
            border-top-left-radius: 8px;
            border-bottom-left-radius: 8px;
            padding: 0 10px;
            outline: none;
            &:focus{
                border-color: #F4C57F;
            }
        }
        button{
            display: inline-block;
            width: 100%;
            padding: 10px 0 13px 0;
            background: #000;
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
            box-shadow: 0 2px 4px rgba(0,0,0,.11);
            border:0;
            text-align:center;
            color: #fff;
            text-decoration: none;
            font-size: 17px;
            letter-spacing: .5px;
            font-weight: 800;
            transition: 300ms all;
            outline: none;
            margin: 0;
            cursor: pointer;
            svg{
                margin-right: 10px;
                transition: 250ms transform;
                vertical-align: middle;
            }
            &:hover{
                box-shadow: 0px 5px 11px rgba(0,0,0,.18);
            }
        }
    }
`

const Notice = styled.p`
    padding-top: 18px;
    font-size: 13px;
    color: #676767;
    text-align: center;
`;

const SectionTitle = styled.div`
    text-align: left;
    & > div{
        font-size: 25px;
        font-weight: 700;
        padding-bottom: 38px;
        padding-left: 10px;
        display:inline-block;
        p{
            /* text-align:left; */
        }
        div{
            margin-top: 9px;
            background: rgba(244,189,97,0.85);
            width: calc(100% + 30px);
            height: 6px;
        }
    }
`;

class Subscribe extends Component {
    
    constructor(props){
        super(props);
        this.state = {email: "", buttonText: "SUBSCRIBE"};

        this.handleForm = this.handleForm.bind(this);
    }

    handleForm = e => {
        e.preventDefault();
        let dis = this;
        return axios.post(
            "https://formcarry.com/s/GJgKchWJ6F_", 
            this.state, 
            {headers: {"Accept": "application/json"}}
        )
        .then(function (response) {
            if(response.data.code == 200){
                dis.setState({buttonText: "DONE", email: ""});

                alert("Thank you, you will get an confirmation email, accept it and you are done.");
            }else{
                alert("Something terribly went wrong");
            }
        })
        .catch(function (error) {
            alert("Something terribly went wrong");
        });
    }

    handleFields = e => this.setState({ [e.target.name]: e.target.value });

    render() {
        return (
            <Container>
                <SectionTitle>
                    <div>
                        <p>
                            Seeking more deals?
                        </p>
                        <div></div>
                    </div>
                </SectionTitle>

                <ShadowDiv>
                    <form onSubmit={this.handleForm} method="POST">
                        <input type="text" placeholder="myawesome@email.com" name="email" value={this.state.email} onChange={this.handleFields} required/>
                        <button type="Submit" disabled={(this.state.buttonText == "DONE" ? true : false)}>{(this.state.buttonText == "DONE" ? <CheckIcon/> : <EmailIcon/>)} {this.state.buttonText}</button>
                    </form>
                </ShadowDiv>

                <Notice>
                    If I find more deals, will let you know. No spams or kinda shit, promises.
                </Notice>
            </Container>
        );
    }
}

export default Subscribe;